import React, { useState, useEffect } from 'react';
import './Styles/Fields.css';
import { useNavigate } from 'react-router-dom';

function Fields() {
    const navigate = useNavigate();
    const [clickedBtn, setClickedBtn] = useState(null); //   
    const [visitedBtns, setVisitedBtns] = useState({}); //   

    //     -sessionStorage 
    useEffect(() => {
        const savedVisited = JSON.parse(sessionStorage.getItem('visitedBtns')) || {};
        setVisitedBtns(savedVisited);
    }, []);

    //   -sessionStorage 
    const handleClick = (btnName, path) => {
        const updatedVisited = { ...visitedBtns, [btnName]: true };
        setVisitedBtns(updatedVisited);
        sessionStorage.setItem('visitedBtns', JSON.stringify(updatedVisited));
        navigate(path);
    };

    return (
        <div className="Fields">
            <div className='white-circle-Fields'>
                <h1 className="titleHapak"> </h1>
                <div>
                <p className='txt-fields'>
                 ,          .<br /><br />

                    " ,       , ,  ,  .<br /><br />
                             (, ,   '), " ,     . <br/><br/>
                <img className='FieldsImg' src={`${process.env.PUBLIC_URL}/assets/imgs/Fields.jpg`} alt="" />

                   (" / " / ")         .<br /><br />

                        "   , , "  .<br /><br />

                    (" , " ")   ,       ,     .
                </p>



                    {/*    */}
                    <div className='navigation-btn'>
                        <div onClick={() => navigate("/war")} className='prev'>
                            <p className='text-lable'> </p>
                            <img className="arrow right" src={`${process.env.PUBLIC_URL}/assets/imgs/nextArrowGrey.png`} alt="" />
                        </div>
                        <div onClick={() => navigate("/menu")} className='next'>
                            <p className='text-lable'></p>
                            <img className="arrow left" src={`${process.env.PUBLIC_URL}/assets/imgs/nextArrowGrey.png`} alt="" />
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default Fields;
